.social {
    margin-bottom: 40px;
    width: 100%;
    min-width: 100%;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: space-between;

    .button {
        min-width: 33%;
        display: flex;
        flex-direction: row;
        outline: none;
        border: none;
        align-items: center;
        margin-bottom: 20px;
        padding-right: 40px;

        @media screen and (max-width: 576px) {
            min-width: 100%;
            padding-right: 0px;
        }

        &:hover {
            cursor: pointer;
        }

        .icon {
            width: 50px;
            height: 50px;
            margin-right: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .buttonText {
            font-size: 20px;
            font-weight: 900;
            display: -webkit-box;
            max-width: 400px;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }

    }
}